
.bar{
  top: 0;
  text-align: right;
  height:5.97%;
  width: 100%;
  background:#fff;
  -webkit-box-shadow: 2px 2px 5px rgba(190, 190, 190, 0.2);
          box-shadow: 2px 2px 5px rgba(190, 190, 190, 0.2);
  position: fixed;
  z-index: 99;
  overflow:hidden;
  vertical-align: middle;
}

.App-mini{
  margin-right: -10px;
  -webkit-transition: Scale 600ms ease-out;
  -o-transition: Scale 600ms ease-out;
  transition: Scale 600ms ease-out;
}

.minimize-enter{
  -webkit-transform: translateY(0%);
      -ms-transform: translateY(0%);
          transform: translateY(0%);
}

.minimize-leave{
  -webkit-transform: translateY(200%);
      -ms-transform: translateY(200%);
          transform: translateY(200%);
}

.socialNetwork{
  display: inline-block;
  -webkit-transform: translateY(10%);
      -ms-transform: translateY(10%);
          transform: translateY(10%);
  margin-right: 10px;
  width: 150px;
}

.socialNetwork > li{

}

.socialNetwork > li > img {
  padding: 5px;
  width: 10px;
}



