
.App {
  text-align: center;
}
.full{
  height: 100%;
}
.blue{
  background-color: blue
}
.red{
  background-color: red
}

.beige{
  background-color: #F7F2E0
}

#Toppage{
  color: white
}

.Pages{

}

.Container{
  position: relative;
  top: 14%;
  height: 80%;
}

.rect{
  position:relative;
  display: inline-block;
  text-align: center;
  margin: 0 auto;
  width: 50%;
}

.miniRect{
  position: relative;
  display: inline-block;
  margin: 0;
  width: 50%
}

@media screen and (min-width: 1900px) {
  .miniRect{
    height: 400px;
  }

}

.green {
  background-color: green
}

.App-logo {
  margin-top: -5.97%;
  width: 18%;
}

.App-mini{
  position: relative;
  -webkit-transition : 300ms ease;
  -o-transition : 300ms ease;
  transition : 300ms ease;
  width: 7%;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  text-transform: lowercase;
  font-size: 2vw;
  font-weight: 700;
  font-family: 'Montserrat', sans-serif;
  margin-bottom: 2%;
}

.App-intro {
  margin: 5% 0% 1% 0%;
  font-size: 12pt;
}
.bold{
  font-weight: 700
}

.right{
  float: right
}

.fontblue{
  color: #1c51FF
}


@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



