.carousel-slide-enter{
  -webkit-transform : translate(100%);
      -ms-transform : translate(100%);
          transform : translate(100%);

}

.carousel-slide-enter.carousel-slide-enter-active {
  -webkit-transform: translate(0%);
      -ms-transform: translate(0%);
          transform: translate(0%);
  -webkit-transition:-webkit-transform 800ms linear;
  transition:-webkit-transform 800ms linear;
  -o-transition:transform 800ms linear;
  transition:transform 800ms linear;
  transition:transform 800ms linear, -webkit-transform 800ms linear;
}

.carousel-slide-leave{
  -webkit-transform : translate(0%);
      -ms-transform : translate(0%);
          transform : translate(0%);
  z-index: -1
}

.carousel-slide-leave.carousel-slide-leave-active{
  -webkit-transform: translate(-100%);
      -ms-transform: translate(-100%);
          transform: translate(-100%);
  /* opacity: 0; */
  -webkit-transition: -webkit-transform 800ms linear;
  transition: -webkit-transform 800ms linear;
  -o-transition: transform 800ms linear;
  transition: transform 800ms linear;
  transition: transform 800ms linear, -webkit-transform 800ms linear;
}

.Carousel{
  top:0;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
}

.indicator{
  bottom: 10px;
  width: auto;
  margin: auto;
  text-align: center;

}

.indicator-point{
  display: inline-block;
  cursor: pointer;
  height: 10px;
  width: 10px;
  margin-left: 1%;
  border: 1px solid white;
  border-radius: 100%;
}



