
ul{
  width: 80%;
  padding: 0;
  margin : auto;
  display: inline-block;
}
nav hr{
  display: none
}

li > a {
  font-weight: 700;
  color: black;
  -webkit-transition: 200ms linear;
  -o-transition: 200ms linear;
  transition: 200ms linear;
  text-transform: lowercase;
  letter-spacing: 0.2px;
}

a {
  text-decoration: none
}

li {
  display: inline-block;
  padding: 3px 5%;
  cursor: pointer;
  list-style-type:none;
}
a:hover, li:hover > a{
  /* color: #aaa; */
  background-color: inherit !important;

}
a.active {
  color: #111;
}

ul > li.active {
  /* border-bottom: 3px solid black; */
}

.NavVrt{
  text-align: center;
  top: 50%;
  z-index: 2;
  -webkit-transform: rotateZ(-90deg);
      -ms-transform: rotate(-90deg);
          transform: rotateZ(-90deg);
  -webkit-transition: 500ms;
  -o-transition: 500ms;
  transition: 500ms;
  overflow-x: hidden;
}

.numPage{
  display: inline-block;
  font-size: 1.2em;
  font-weight: 700;
  padding: 0 2px 2px 0;
  border-bottom: 2px solid black;
  -webkit-transform: rotateZ(90deg);
      -ms-transform: rotate(90deg);
          transform: rotateZ(90deg);
  -webkit-transition: 500ms;
  -o-transition: 500ms;
  transition: 500ms;
}

.titlePage{
  display: inline-block;
  padding: 0 20px;
  font-size: 0.8em;
  text-transform: lowercase;
}

.anime{
  -webkit-animation: anime--rotate 400ms 1;
          animation: anime--rotate 400ms 1
}

@media screen and (min-width: 768px) {
    nav hr {
      height: 3px;
      margin: 0;
      background: #000;
      border: none;
      -webkit-transition: .3s ease-in-out;
      -o-transition: .3s ease-in-out;
      transition: .3s ease-in-out;
      display: block;
    }
}
@media screen and (min-width: 1900px) {
  .numPage{
    font-size: 1.4em;
  }
  .titlePage{
    font-size: 1em;
  }
}

@-webkit-keyframes anime--rotate {
  from {
    -webkit-transform: rotateZ(-90deg) rotateX(0deg);
            transform: rotateZ(-90deg) rotateX(0deg)
  }
  to{
    -webkit-transform: rotateZ(-90deg) rotateX(-360deg);
            transform: rotateZ(-90deg) rotateX(-360deg)
  }
}

@keyframes anime--rotate {
  from {
    -webkit-transform: rotateZ(-90deg) rotateX(0deg);
            transform: rotateZ(-90deg) rotateX(0deg)
  }
  to{
    -webkit-transform: rotateZ(-90deg) rotateX(-360deg);
            transform: rotateZ(-90deg) rotateX(-360deg)
  }
}



